import './App.css';
import styled from 'styled-components';
//import Logo from '../assets/img/logo.png';
import React, {Fragment} from 'react';
import {HashRouter as Router, Route, Switch} from 'react-router-dom';
import { HashLink as Link } from 'react-router-hash-link';
import Web3 from 'web3'
import Canvas from '../merge/Canvas';
import PFP from '../merge/PFP';
import Home from './home';
import NFT from './nft';
import NFTEarly from './nftearly';
import TOKEN from './token';
import Wallet from './wallet';
import Dirtypanties from './dirtypanties';
import NFTNAME from './nftname';
import ClaimSeason0 from './season0claim';
import NFTSTAIN from './nftstain';
import NFTSETSTAIN from './nftsetstain';
import Dropdown from 'react-bootstrap/Dropdown'
import discord  from '../assets/img/discord.png';
import discordwhite  from '../assets/img/discord-white.png';
import twitter  from '../assets/img/twitter.png';
import twitterwhite  from '../assets/img/twitter-white.png';
import insta from '../assets/img/insta.png';
import instawhite from '../assets/img/insta-white.png';
import opensea from '../assets/img/opensea-white.png';


var namehash = require('eth-ens-namehash')


declare let window: any;

class App extends React.Component <any, any> {

  constructor(props) {
    super(props)
    this.state = {
      account: '',
      networkId: 0
    }
  }

  networkId = 1;
  web3 = null;
  ens = null;
  async componentDidMount() {
    await this.loadWeb3()
    if(this.web3 !== null){
      await this.loadNetwork()
      await this.connect()
    }
  }

    async componentDidUpdate(prevProps) {
       if( this.state.account === '' && this.web3 !== null){
        await this.loadNetwork()
        await this.connect()
        }
  }

  async loadWeb3() {
    await window.ethereum.enable()
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum)
      this.web3 = window.web3
    }
    else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider)
      this.web3 = window.web3
   }
    if (window.ethereum || window.web3) {
      // use MetaMask's provider
      // detect Metamask account change
      window.ethereum.on('accountsChanged', (accounts) => {
        this.setState({
          account: accounts[0]
        })
        this.connect()
      });
       // detect Network account change
      window.ethereum.on('networkChanged', (newNetworkId) => {
        //console.log("newNetworkId",parseInt(newNetworkId))
        this.setState({
          networkId: parseInt(newNetworkId)
        })
        this.connect()
      });
    } else {
      console.warn(
        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
      );
    }
  }


  wait(ms) {
    return new Promise((resolve, reject) => setTimeout(resolve, ms));
  }

  async loadNetwork() {
    this.setState({
      networkId: await this.web3.eth.net.getId()
    });
  }


async Reverse(address) {
    if ( this.state.networkId === 1){
        try{
        var lookup = address.toLowerCase().substr(2) + '.addr.reverse'
        var ResolverContract = await this.web3.eth.ens.resolver(lookup);
        var nh = namehash.hash(lookup);
        var name = await ResolverContract.methods.name(nh).call()
        //console.log("ENS name",name)
        return name;
        }
        catch (error) {
             return "";
        }
    }
    else{
        return "";
    }
}

  async connect() {

    this.loadWeb3()

    const walletAddress = await window.ethereum.request({
      method: "eth_requestAccounts",
      params: [
        {
          eth_accounts: {}
        }
      ]
    });
    if (!walletAddress[0]) {
    // Runs only they are brand new, or have hit the disconnect button
      await window.ethereum.request({
        method: "wallet_requestPermissions",
        params: [
          {
            eth_accounts: {}
          }
        ]
      });
    }
    const accounts = await this.web3.eth.getAccounts()
    console.log("CONNECT before ENS",accounts[0])
    const ens = await this.Reverse(accounts[0])
    this.setState({ account: accounts[0] })
    this.setState({ ens: ens })
    console.log("CONNECT After ENS",accounts[0],ens)
  }

  async disconnect() {
    this.setState({
      account: '',
    });
    this.web3 = null
    this.ens = ""
    console.log(this.web3,this.ens)
  }


CheckWalletConnection = () => {
    //console.log(this.state.networkId,this.state.account.length)
    if(  (this.state.account.length > 0) && (this.state.networkId === 1 || this.state.networkId === 4 || this.state.networkId === 5777 )){
      return <ConnectButton onClick={() => {this.disconnect() }}><Connect/> {this.state.ens === '' ? `${this.state.account.substr(0,6)}..${this.state.account.substr(38,4)}` : this.state.ens}</ConnectButton>
    }
    else if (( this.state.networkId !==1 || this.state.networkId !== 4 || this.state.networkId !== 5777 ) && (this.state.account.length >0) ){
        //console.log("wRONG",this.state.networkId,this.state.account.length)
        return <ConnectButton onClick={() => {this.disconnect() }}> WRONG NETWORK</ConnectButton>
    }
    else if ((this.state.networkId !==1 || this.state.networkId !== 4 || this.state.networkId !== 5777) && (this.state.account.length ===0) ){
      //console.log("Account not detected",this.state.networkId,this.state.account.length)
      return <ConnectButton onClick={() => {this.connect() }}>Connect</ConnectButton>;
    }
  }

  render() {
    return (
            <Router basename="/">
                <NavWrapper>
                  <NavContainer>
                    <LeftNav>
                     <StyledNav>
                          <StyledLinkH to="/">
                          <TextLogo>DirtyPanties<span>NFT</span></TextLogo>
                          </StyledLinkH>
                      </StyledNav>
                      {this.web3 && ( this.state.networkId === 4 || this.state.networkId === 5777 ) &&
                       <StyledNavDrop>
                        <TextDashboard>&nbsp;|&nbsp;&nbsp;&nbsp;</TextDashboard>
                      <Dropdown>
                          <Dropdown.Toggle variant="transparent" id="dropdown-toggle" style={{marginTop: -0.2 + 'em'}}>
                            <StyledLink>Members</StyledLink>
                          </Dropdown.Toggle>
                          <Dropdown.Menu variant="dark" id="dropdown-menu">
                            <Dropdown.Item id="dropdown-item" href="/#/nftearly"><StyledLink>(Early Access) Buy a NFT</StyledLink></Dropdown.Item>
                            <Dropdown.Item id="dropdown-item" href="/#/nft"><StyledLink>Buy a NFT</StyledLink></Dropdown.Item>
                            <Dropdown.Item id="dropdown-item" href="/#/token"><StyledLink>Claim $PNM</StyledLink></Dropdown.Item>
                            <Dropdown.Item id="dropdown-item" href="/#/wallet"><StyledLink>NFT Wallet</StyledLink></Dropdown.Item>
                          </Dropdown.Menu>
                        </Dropdown>
                       </StyledNavDrop>
                      }
                      <TextDashboard>&nbsp;|&nbsp;&nbsp;&nbsp;</TextDashboard>
                    <SocialWrapper>
                        <SocialLogo  href={'https://twitter.com/DirtyPantiesnft'} target="_blank">
                            <ImgWrapper src={twitterwhite} height={`15px`} alt="twitter"/>
                        </SocialLogo>
                        <SocialLogo href={`https://discord.gg/zASPSkRTsu`} target="_blank">
                            <ImgWrapper src={discordwhite} height={`16px`} alt="discord"/>
                        </SocialLogo>
                       <SocialLogo href={`https://opensea.io/collection/dirtypantiesseason0`} target="_blank">
                            <ImgWrapper src={opensea} height={`16px`} alt="insta"/>
                        </SocialLogo>
                        <SocialLogo href={`https://www.instagram.com/dirtypantiesnft`} target="_blank">
                            <ImgWrapper src={instawhite} height={`16px`} alt="insta"/>
                        </SocialLogo>
                    </SocialWrapper>
                   </LeftNav>
                   <this.CheckWalletConnection/>
                  </NavContainer>
                <Fragment>
                    <Switch>
                        <Route exact path={'/'} render={() => <Home web3={this.web3} account={this.state.account}/>} />
                        <Route exact path={'/nft'} render={() => <NFT  web3={this.web3} account={this.state.account}/>} />
                        <Route exact path={'/nftearly'} render={() => <NFTEarly  web3={this.web3} account={this.state.account}/>} />
                        <Route exact path={'/token'} render={() => <TOKEN web3={this.web3} account={this.state.account}/>} />
                        <Route exact path={'/wallet'} render={(props) => <Wallet web3={this.web3} account={this.state.account} {...props}  />} />
                        <Route path={'/dirtypanties/:tokenId'} render={(props) => <Dirtypanties web3={this.web3} account={this.state.account} {...props}/>} />
                        <Route path={'/nftname/:tokenId'} render={(props) => <NFTNAME web3={this.web3} account={this.state.account} {...props}/>} />
                        <Route path={'/nftstain/:tokenId'} render={(props) => <NFTSTAIN web3={this.web3} account={this.state.account} {...props}/>} />
                        <Route path={'/nftsetstain/:tokenId'} render={(props) => <NFTSETSTAIN web3={this.web3} account={this.state.account} {...props}/>} />
                        <Route path={'/season0claim/:tokenId'} render={(props) => <ClaimSeason0 web3={this.web3} account={this.state.account} {...props}/>} />
                        <Route path={'/canvas'} render={(props) => <Canvas />} />
                        <Route path={'/pfp'} render={(props) => <PFP />} />
                        </Switch>
                </Fragment>
                </NavWrapper>
        </Router>
    );
  }
}



